.UserInfoContainer {
  display: flex;
  flex-wrap: wrap;
}

.UserInfo {
  box-sizing: border-box;
  margin: 10px;
  cursor: pointer;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100px;
  height: 100px;
  color: black;
  background: #dedede;
  padding: 5px;
}

.SelectedUserInfo {
  border: 5px solid blue;
}
